import { connect } from 'react-redux';
import { restartCalculator } from '../../../store/questions/questions.actions';
import { resetResults } from '../../../store/results/results.actions';
import OutsidePolicy from './OutsidePolicy';

const mapStateToProps = (state, ownProps) => ({});

const mapDispatchToProps = dispatch => ({
  onProceedClick: () => {
    dispatch(restartCalculator());
    dispatch(resetResults());
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(OutsidePolicy);
