import {
  SAVE_ANSWER,
  GOTO_STEP_BY_NUMBER,
  GOTO_NEXT_STEP,
  GOTO_PREVIOUS_STEP,
  RESTART_CALCULATOR,
} from './questions.types';

export const saveAnswer = (stepName, applicantOne = '', applicantTwo = '') => ({
  type: SAVE_ANSWER,
  data: { stepName, applicantOne, applicantTwo },
});

export const gotoStepByNumber = stepNumber => ({
  type: GOTO_STEP_BY_NUMBER,
  stepNumber,
});

export const gotoNextStep = () => ({ type: GOTO_NEXT_STEP });

export const gotoPreviousStep = () => ({ type: GOTO_PREVIOUS_STEP });

export const restartCalculator = () => ({ type: RESTART_CALCULATOR });
