import { connect } from 'react-redux';
import {
  selectAboutYouData,
  selectIncomeData,
  selectOutgoingsData,
} from '../../../store/questions/questions.selectors';
import { resetResults } from '../../../store/results/results.actions';
import { postCalculatorDataThunk } from '../../../store/results/results.thunks';
import SummaryStep from './SummaryStep';
import { gotoNextStep } from '../../../store/questions/questions.actions';

const mapStateToProps = state => ({
  aboutYouData: selectAboutYouData(state),
  incomeData: selectIncomeData(state),
  outgoingsData: selectOutgoingsData(state),
  state,
});

const mapDispatchToProps = dispatch => ({
  onClickProceed: () => {
    dispatch(resetResults());
    dispatch(postCalculatorDataThunk());
    dispatch(gotoNextStep());
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(SummaryStep);
