import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { clone } from 'ramda';
import CurrencyInput from '@aib/uikit-react/dist/Form/CurrencyInput/CurrencyInput';
import FormControl from '@aib/uikit-react/dist/Form/FormControl/FormControl';
import Label from '@aib/uikit-react/dist/Form/InputLabel/InputLabel';
import Button from '@aib/uikit-react/dist/Form/Button/Button';
import * as TS from '../../templates/Template.styled';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';
import ApplicantLabel from '@aib/uikit-react/dist/Form/ApplicantLabel/ApplicantLabel';
import Card from '../../BuildingBlocks/Card/Card';
import * as S from './OutgoingsStep.styled';
import { outgoingsFields } from './OutgoingsStep.question';
import Add from '../../Theme/icons/Add';
import Remove from '../../Theme/icons/Remove';

function getInitialState() {
  return outgoingsFields.reduce((accumulator, currentValue) => {
    const { fieldName } = currentValue;
    accumulator[fieldName] = [''];
    return accumulator;
  }, {});
}

const MAX_DIGITS = 6;

function OutgoingsStep({
  onClickContinue,
  currentStoreValue,
  onChangeValue,
  isJoint,
  stepName,
}) {
  const { applicantOne, applicantTwo } = currentStoreValue;

  const [formState, setFormState] = useState([
    applicantOne || getInitialState(),
    applicantTwo || getInitialState(),
  ]);

  const [activeApplicantIndex, setActiveApplicantIndex] = useState(0);
  const [activeFieldName, setActiveFieldName] = useState('');
  const [activeInputIndex, setActiveInputIndex] = useState(0);

  const handleInputChange = (
    val = '',
    applicantIndex,
    fieldName,
    fieldIndex
  ) => {
    let newState = clone(formState);

    newState[applicantIndex][fieldName][fieldIndex] = val;

    setFormState(newState);
  };

  function handleProceedClick() {
    const formStateTrimmed = trimBlanksExceptOne(formState);
    onChangeValue(
      stepName,
      formStateTrimmed[0],
      isJoint ? formStateTrimmed[1] : null
    );
    onClickContinue();
  }

  const trimBlanksExceptOne = formState => formState.map(trimBlanksApplicant);
  const trimBlanksApplicant = applicantState => {
    const out = {};
    for (const [key, arr] of Object.entries(applicantState)) {
      out[key] = arr.filter(val => !!val);
      out[key] = out[key].length === 0 ? [''] : out[key];
    }
    return out;
  };

  function handleFocus(applicantIndex, fieldName, arrayIndex) {
    setActiveApplicantIndex(applicantIndex);
    setActiveFieldName(fieldName);
    setActiveInputIndex(arrayIndex);
  }
  function handleBlur() {
    setActiveFieldName('');
  }

  function handleCloseClick() {
    setActiveFieldName('');
  }

  function renderField(fieldDefinition, applicantIndex) {
    const applicantState = formState[applicantIndex];
    const { fieldName, maxValues } = fieldDefinition;

    const valuesArray = applicantState[fieldName];

    return (
      <S.IncomeField key={fieldName}>
        {valuesArray.map((inputValue, inputIndex) =>
          renderInput(fieldDefinition, applicantIndex, inputIndex)
        )}
        {maxValues > 1 && renderAddMoreButton(applicantIndex, fieldDefinition)}
      </S.IncomeField>
    );
  }

  function renderInput(fieldDefinition, applicantIndex, inputIndex) {
    const { fieldName, label, placeholder } = fieldDefinition;
    const applicantState = formState[applicantIndex];

    const fieldValue = applicantState[fieldName][inputIndex];

    const isFieldActive =
      applicantIndex === activeApplicantIndex &&
      fieldName === activeFieldName &&
      inputIndex === activeInputIndex;
    const isAnyFieldActive = activeFieldName !== '';
    const isOpaque = isFieldActive || !isAnyFieldActive;
    const testId = `mcx-calculator-outgoings-${fieldName}-applicant-${applicantIndex +
      1}-${inputIndex}`;
    return (
      <S.InputContainer
        key={`${applicantIndex}_${fieldName}_${inputIndex}`}
        isOpaque={isOpaque}
        testId={testId}
      >
        <TS.QuestionContent>
          {isFieldActive && (
            <Card
              isVisible={isFieldActive}
              title={fieldDefinition.questionText}
              body={fieldDefinition.hint}
              onClickClose={handleCloseClick}
            />
          )}
          <FormControl>
            <Label>{label}</Label>
            <CurrencyInput
              autoFocus={false}
              id=""
              fullWidth={true}
              name=""
              maxDigits={MAX_DIGITS}
              placeholder={placeholder}
              onChange={value =>
                handleInputChange(value, applicantIndex, fieldName, inputIndex)
              }
              value={fieldValue || ''}
              type=""
              onFocus={() => handleFocus(applicantIndex, fieldName, inputIndex)}
              onBlur={() => handleBlur()}
            />
            {inputIndex > 0 &&
              renderDeleteButton(applicantIndex, fieldDefinition, inputIndex)}
          </FormControl>
        </TS.QuestionContent>
      </S.InputContainer>
    );
  }

  function renderDeleteButton(applicantIndex, fieldDefinition, inputIndex) {
    return (
      <S.Remove
        onClick={() =>
          handleDeleteClick(applicantIndex, fieldDefinition, inputIndex)
        }
      >
        <Remove />
      </S.Remove>
    );
  }

  function handleDeleteClick(applicantIndex, fieldDefinition, inputIndex) {
    const { fieldName } = fieldDefinition;
    let newState = clone(formState);

    const valuesArray = newState[applicantIndex][fieldName];

    valuesArray.splice(inputIndex, 1);
    setFormState(newState);
  }

  function renderAddMoreButton(applicantIndex, fieldDefinition) {
    const { fieldName, maxValues } = fieldDefinition;
    let newState = clone(formState);

    const valuesArray = newState[applicantIndex][fieldName];

    const isVisible = valuesArray[0] !== '' && valuesArray.length < maxValues;
    const isOpaque = activeFieldName === '' || activeFieldName === fieldName;

    return (
      <S.AddMoreButtonContainer hidden={!isVisible} isOpaque={isOpaque}>
        <Button
          label="Add more"
          buttonStyle="text"
          icon={<Add />}
          onClick={() => handleAddMoreClick(applicantIndex, fieldDefinition)}
        />
      </S.AddMoreButtonContainer>
    );
  }

  function handleAddMoreClick(applicantIndex, fieldDefinition) {
    const { fieldName, maxValues } = fieldDefinition;
    let newState = clone(formState);

    const valuesArray = newState[applicantIndex][fieldName];

    valuesArray.length < maxValues && valuesArray.push('');
    setFormState(newState);
  }

  const isMainQuestionVisible = activeFieldName === '';

  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <S.MainQuestionContainer isVisible={isMainQuestionVisible}>
          <MainQuestion
            questionText="Will you have any monthly repayments after you drawdown your mortgage?"
            hint=""
          />
        </S.MainQuestionContainer>
      </TS.QuestionHeader>

      <TS.QuestionContent>
        {isJoint && <ApplicantLabel applicantNumber={1} />}
        <br />
        {outgoingsFields.map(inputConfig => renderField(inputConfig, 0))}
      </TS.QuestionContent>

      {isJoint && (
        <TS.QuestionContent>
          <br />
          <br />
          <br />
          <ApplicantLabel applicantNumber={2} />
          {outgoingsFields.map(inputConfig => renderField(inputConfig, 1))}
        </TS.QuestionContent>
      )}

      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton onClick={handleProceedClick} isDisabled={false} />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

OutgoingsStep.propTypes = {
  onClickContinue: PropTypes.func.isRequired,
  currentStoreValue: PropTypes.any,
  onChangeValue: PropTypes.func.isRequired,
  isJoint: PropTypes.bool.isRequired,
  stepName: PropTypes.string.isRequired,
};
export default OutgoingsStep;
