import { connect } from 'react-redux';
import {
  gotoPreviousStep,
  gotoStepByNumber,
} from '../store/questions/questions.actions';
import {
  selectCurrentStep,
  selectCurrentStepNumber,
} from '../store/questions/questions.selectors';
import Calculator from './Calculator';

const mapDispatchToProps = dispatch => ({
  onClickBack: () => dispatch(gotoPreviousStep()),
  gotoStepNumber: stepNumber => dispatch(gotoStepByNumber(stepNumber)),
});

const mapStateToProps = (state, ownProps) => {
  console.log({ ownProps });
  return {
    ...ownProps,
    currentStep: selectCurrentStep(state),
    currentStepNumber: selectCurrentStepNumber(state),
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Calculator);
