import React from 'react';
import PropTypes from 'prop-types';
import { animated, useTransition } from 'react-spring';
import Steps from '../Steps.list';
import * as S from './Step.styled';

function Step({
  currentQuestionData,
  currentStoreValue,
  onClickContinue,
  onChangeValue,
  isJoint,
  stepName,
  isAnimationForward,
}) {
  function handleValueChange(key, applicantOne, applicantTwo) {
    onChangeValue(key, applicantOne, applicantTwo);
  }

  function handleClickContinue() {
    onClickContinue();
  }

  const commonProps = {
    question: currentQuestionData,
    currentStoreValue,
    onChangeValue: handleValueChange,
    onClickContinue: handleClickContinue,
    isJoint,
    stepName,
  };

  const transitions = useTransition(stepName, null, {
    from: {
      opacity: 0,
      transform: isAnimationForward
        ? 'translate3d(100%,0,0)'
        : 'translate3d(-100%,0,0)',
    },
    enter: {
      opacity: 1,
      transform: 'translate3d(0%,0,0)',
    },
    leave: {
      opacity: 1,
      transform: isAnimationForward
        ? 'translate3d(-100%,0,0)'
        : 'translate3d(100%,0,0)',
    },
  });

  return transitions.map(({ item, props, key }) => {
    return (
      <animated.div key={key} style={props}>
        <S.StepContainer>
          {React.createElement(Steps[item], commonProps)}
        </S.StepContainer>
      </animated.div>
    );
  });
}

Step.propTypes = {
  isAnimationForward: PropTypes.bool.isRequired,
  currentQuestionData: PropTypes.object,
  currentStoreValue: PropTypes.any,
  onClickContinue: PropTypes.func.isRequired,
  onChangeValue: PropTypes.func.isRequired,
  isJoint: PropTypes.bool.isRequired,
  stepName: PropTypes.string.isRequired,
};

export default Step;
