import React from 'react';
import PropTypes from 'prop-types';

import IconLeftArrow from '@aib/uikit-react/dist/Icons/IconLeftArrow';
import IconClose from '@aib/uikit-react/dist/Icons/IconClose';
import UikitToolbar from '@aib/uikit-react/dist/Navigation/TopAppBar/TopAppBar';
import * as S from './Toolbar.styled';

function Toolbar({
  title,
  hasBackButton,
  hasCloseButton,
  onClickBack,
  onClickClose,
}) {
  function handleBackClick() {
    hasBackButton && onClickBack();
  }

  function handleCloseClick() {
    hasCloseButton && onClickClose();
  }

  return (
    <UikitToolbar>
      <IconLeftArrow
        onClick={handleBackClick}
        style={{ opacity: hasBackButton ? 1 : 0 }}
      />
      {title && <S.ToolbarTitle>{title}</S.ToolbarTitle>}
      <IconClose
        onClick={handleCloseClick}
        style={{ opacity: hasCloseButton ? 1 : 0 }}
      />
    </UikitToolbar>
  );
}

Toolbar.propTypes = {
  title: PropTypes.string.isRequired,
  hasBackButton: PropTypes.bool.isRequired,
  hasCloseButton: PropTypes.bool.isRequired,
  onClickBack: PropTypes.func.isRequired,
  onClickClose: PropTypes.func.isRequired,
};

export default Toolbar;
