import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { clone } from 'ramda';
import CurrencyInput from '@aib/uikit-react/dist/Form/CurrencyInput/CurrencyInput';
import FormControl from '@aib/uikit-react/dist/Form/FormControl/FormControl';
import Label from '@aib/uikit-react/dist/Form/InputLabel/InputLabel';
import { euroValueToNumber } from '../../../util/euroFormatter';
import * as TS from '../../templates/Template.styled';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';
import ApplicantLabel from '@aib/uikit-react/dist/Form/ApplicantLabel/ApplicantLabel';
import Card from '../../BuildingBlocks/Card/Card';
import * as S from './IncomeStep.styled';
import { incomeFields } from './IncomeStep.question';

const MAX_DIGITS = 7;

function emptyApplicantState() {
  return incomeFields.reduce((accumulator, fieldDefinition) => {
    const { fieldName } = fieldDefinition;
    accumulator[fieldName] = '';
    return accumulator;
  }, {});
}

function IncomeStep({
  onClickContinue,
  currentStoreValue,
  onChangeValue,
  isJoint,
  stepName,
}) {
  const [formState, setFormState] = useState(
    getInitialState(isJoint, currentStoreValue)
  );
  const [activeFieldName, setActiveFieldName] = useState('');
  const [activeApplicantIndex, setActiveApplicantIndex] = useState(0);

  useEffect(() => {
    setFormState(getInitialState(isJoint, currentStoreValue));
  }, [isJoint, currentStoreValue]);

  function getInitialState(isJoint, currentStoreValue) {
    const { applicantOne, applicantTwo } = currentStoreValue;
    const initialState = [
      applicantOne || emptyApplicantState(),
      isJoint ? applicantTwo || emptyApplicantState() : undefined,
    ];
    return initialState;
  }

  const handleInputChange = (
    val = '',
    fieldName,
    applicantIndex,
    fieldDefinition
  ) => {
    // Never show an error message
    // Max 9,999,999
    const { isZeroAllowed } = fieldDefinition;
    const restrictedValue =
      euroValueToNumber(val) <= 0 && !isZeroAllowed ? '' : val;
    const newState = clone(formState);
    const newApplicantState = newState[applicantIndex] || {};
    newApplicantState[fieldName] = restrictedValue;
    setFormState(newState);
  };

  function handleProceedClick() {
    onChangeValue(stepName, formState[0], formState[1]);
    onClickContinue();
  }

  function isFinished(fields = {}) {
    const { basic = '' } = fields;
    return euroValueToNumber(basic) > 0;
  }

  function handleFocus(fieldName, applicantIndex) {
    // TODO: Fix error in console around second callback argument in console
    setActiveFieldName(fieldName, applicantIndex);
    setActiveApplicantIndex(applicantIndex);
  }
  function handleBlur() {
    setActiveFieldName('');
  }

  function handleCloseClick() {
    setActiveFieldName('');
  }

  function isPageFinished() {
    if (isJoint) {
      return isFinished(formState[0]) || isFinished(formState[1]);
    } else {
      return isFinished(formState[0]);
    }
  }

  function renderInput(fieldDefinition, applicantIndex) {
    const { fieldName, label, placeholder } = fieldDefinition;
    const isFieldActive =
      fieldName === activeFieldName && applicantIndex === activeApplicantIndex;
    const isFieldVisible = isFieldActive || !activeFieldName;
    const applicantState = formState[applicantIndex] || {};

    const fieldValue = applicantState[fieldName];
    const testId = `mcx-calculator-income-${fieldName}-applicant-${applicantIndex +
      1}`;
    return (
      <S.IncomeField
        key={fieldName}
        isFieldVisible={isFieldVisible}
        testId={testId}
      >
        <TS.QuestionContent>
          {isFieldActive && (
            <Card
              isVisible={isFieldActive}
              title={fieldDefinition.questionText}
              body={fieldDefinition.hint}
              onClickClose={handleCloseClick}
            />
          )}

          <FormControl>
            <Label>{label}</Label>
            <CurrencyInput
              autoFocus={false}
              fullWidth={true}
              placeholder={placeholder}
              onChange={value =>
                handleInputChange(
                  value,
                  fieldName,
                  applicantIndex,
                  fieldDefinition
                )
              }
              value={fieldValue || ''}
              maxDigits={MAX_DIGITS}
              onFocus={() => handleFocus(fieldName, applicantIndex)}
              onBlur={() => handleBlur()}
            />
          </FormControl>
        </TS.QuestionContent>
      </S.IncomeField>
    );
  }

  const isVisible = activeFieldName === '';

  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <S.MainQuestionContainer isVisible={isVisible}>
          <MainQuestion
            questionText="What type of employment income do you earn?"
            hint="annual amount before tax"
          />
        </S.MainQuestionContainer>
      </TS.QuestionHeader>

      <TS.QuestionContent>
        {isJoint && <ApplicantLabel applicantNumber={1} />}
        <br />
        {incomeFields.map(inputConfig => renderInput(inputConfig, 0))}
      </TS.QuestionContent>

      {isJoint && (
        <TS.QuestionContent>
          <br />
          <br />
          <br />
          <ApplicantLabel applicantNumber={2} />
          {incomeFields.map(inputConfig => renderInput(inputConfig, 1))}
        </TS.QuestionContent>
      )}

      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton
            onClick={handleProceedClick}
            isDisabled={!isPageFinished()}
          />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

IncomeStep.propTypes = {
  onClickContinue: PropTypes.func.isRequired,
  currentStoreValue: PropTypes.any,
  onChangeValue: PropTypes.func.isRequired,
  isJoint: PropTypes.bool.isRequired,
  stepName: PropTypes.string.isRequired,
};
export default IncomeStep;
