import {
  pathEq,
  pick,
  path,
  clone,
  lensProp,
  set,
  compose,
  lensIndex,
} from 'ramda';
import { stepsArray } from '../../ui/Steps/Steps.list';
import { formatAsCurrency } from '@aib/uikit-react/dist/Form/CurrencyInput/currencyFormatter';
import { euroValueToNumber } from '../../util/euroFormatter';
import requestSample from '../../api/request.sample.json';
import { selectTermData, selectMaxTerm } from '../results/results.selectors';

export const selectCurrentStep = state => {
  const questions = state.questions || state;
  return questions.currentStep;
};
export const selectCurrentStepNumber = state =>
  stepsArray.indexOf(selectCurrentStep(state));

export const getStepNameByNumber = stepNumber =>
  stepsArray[stepNumber] || stepsArray[0];

export const getNextStepName = state => {
  const currentStep = selectCurrentStep(state);
  const nextStep = selectNextStep(currentStep);
  return nextStep || currentStep;
};

export const selectIsAnimationForward = state =>
  state.questions.isAnimationForward;

export const selectNextStep = currentStep => {
  const index = stepsArray.findIndex(val => val === currentStep);
  return stepsArray[index + 1];
};

export const selectPreviousStep = currentStep => {
  const index = stepsArray.findIndex(val => val === currentStep);
  return stepsArray[index - 1];
};

export const selectCurrentQuestionData = state => {
  return {};
};

export const selectPercentageComplete = state => {
  const currentStep = selectCurrentStep(state);
  const currentStepIndex = stepsArray.findIndex(val => val === currentStep) + 1;

  return (currentStepIndex * 100) / stepsArray.length;
};

const defaultAnswer = {
  applicantOne: null,
  applicantTwo: null,
};
export const selectCurrentAnswer = state => {
  const currentStep = selectCurrentStep(state);
  const currentValue = state.questions.steps[currentStep] || defaultAnswer;
  return currentValue;
};

export const isJoint = state =>
  pathEq(
    ['questions', 'steps', 'NumberOfApplicantsStep', 'applicantOne'],
    '2',
    state
  );

export const selectNumberOfApplicants = state =>
  path(['steps', 'NumberOfApplicantsStep', 'applicantOne'], state);

const getDataByStepAndApplicant = (state, step, applicant) =>
  path(['questions', 'steps', step, applicant], state);

export const selectRequestData = state => {
  const isJointApplication = isJoint(state);
  let out = clone(requestSample);
  let applicantKeys = ['applicantOne'];
  if (isJointApplication) {
    applicantKeys.push('applicantTwo');
    // add another empty applicants object to the 'out' data structure
    out.applicants.push(clone(out.applicants[0]));
  }

  /*
   * Purchase details
   */
  out = set(
    compose(
      lensProp('purchaseDetails'),
      lensProp('loanAmount')
    ),
    convertToNumber(path(['results', 'purchaseDetails', 'loanAmount'], state)),
    out
  );
  out = set(
    compose(
      lensProp('purchaseDetails'),
      lensProp('purchasePrice')
    ),
    convertToNumber(
      path(['results', 'purchaseDetails', 'purchasePrice'], state)
    ),
    out
  );
  out = set(
    compose(
      lensProp('purchaseDetails'),
      lensProp('loanPurpose')
    ),
    path(['results', 'purchaseDetails', 'loanPurpose'], state),
    out
  );

  /*
   * Applicant details
   */
  for (const [index, applicant] of applicantKeys.entries()) {
    const applicantLens = compose(
      lensProp('applicants'),
      lensIndex(index)
    );
    out = set(
      compose(
        applicantLens,
        lensProp('firstTimeBuyer')
      ),
      // The back end expects true or false rather than 'Yes' or 'No'
      getDataByStepAndApplicant(state, 'FirstTimeBuyerStep', applicant) ===
        'Yes' || false,
      out
    );
    out = set(
      compose(
        applicantLens,
        lensProp('dateOfBirth')
      ),
      // The back end expects YYYY-MM-DD rather than DD / MM / YYYY
      getDataByStepAndApplicant(state, 'DateOfBirthStep', applicant)
        .split('/')
        .reverse()
        .join('-')
        .replace(/ /g, ''),
      out
    );
    out = set(
      compose(
        applicantLens,
        lensProp('dependants')
      ),
      Number(
        getDataByStepAndApplicant(state, 'FinancialDependantsStep', applicant)
      ),
      out
    );
    out = set(
      compose(
        applicantLens,
        lensProp('maritalStatus')
      ),
      // The back end expects 'M' for married, 'S' for single etc)
      getDataByStepAndApplicant(state, 'MaritalStatusStep', applicant).charAt(
        0
      ),
      out
    );

    /*
     * Income details
     */
    const incomeData = getDataByStepAndApplicant(
      state,
      'IncomeStep',
      applicant
    );
    for (let i = 0; i < Object.keys(incomeData).length; i++) {
      out = set(
        compose(
          applicantLens,
          lensProp('income'),
          lensProp(Object.keys(incomeData)[i])
        ),
        convertToNumber(incomeData[Object.keys(incomeData)[i]]),
        out
      );
    }

    /*
     * Outgoings details
     */
    const outgoingsData = getDataByStepAndApplicant(
      state,
      'OutgoingsStep',
      applicant
    );
    for (let i = 0; i < Object.keys(outgoingsData).length; i++) {
      out = set(
        compose(
          applicantLens,
          lensProp('outgoings'),
          lensProp(Object.keys(outgoingsData)[i])
        ),
        convertToNumber(outgoingsData[Object.keys(outgoingsData)[i]]),
        out
      );
    }
  }

  function convertToNumber(value) {
    if (typeof value === 'string') {
      return euroValueToNumber(value);
    } else if (Array.isArray(value)) {
      return value.map(val => euroValueToNumber(val));
    } else {
      return value;
    }
  }

  return out;
};

export const selectIncomeData = state => {
  const incomeStep = path(['questions', 'steps', 'IncomeStep'], state);
  return mapSummaryScreenFinancialData(incomeStep);
};

export const selectOutgoingsData = state => {
  const outgoingsStep = path(['questions', 'steps', 'OutgoingsStep'], state);
  return mapSummaryScreenFinancialData(outgoingsStep);
};

export const selectAboutYouData = state => {
  return pick(
    [
      'NumberOfApplicantsStep',
      'FirstTimeBuyerStep',
      'DateOfBirthStep',
      'MaritalStatusStep',
      'FinancialDependantsStep',
    ],
    path(['questions', 'steps'], state)
  );
};

/*
 * Converts: { applicantOne: { basic: '20000', ... }, applicantTwo: { basic: '10000', ... } }
 * To: { basic: { applicantOne: '20000', applicantTwo: '10000' }, ... }
 * Used for the summary step.
 */
function mapSummaryScreenFinancialData(data) {
  let newStructure = {};

  // Convert values to euro formatted values. Includes converting falsy values to 0:
  const valueFormatter = value => {
    const changeFalsyToZero = val => (!val ? 0 : val);
    return Array.isArray(value)
      ? value.map(val => formatAsCurrency(changeFalsyToZero(val), '€'))
      : formatAsCurrency(changeFalsyToZero(value), '€');
  };

  Object.keys(data).map(key => {
    return (
      data[key] &&
      Object.keys(data[key]).map(incomeType => {
        // add the first applicant's details
        if (!newStructure[incomeType]) {
          return (newStructure[incomeType] = {
            [key]: valueFormatter(data[key][incomeType]),
          });
          // add the second applicant's details if they exist
        } else {
          return (newStructure[incomeType][key] = valueFormatter(
            data[key][incomeType]
          ));
        }
      })
    );
  });

  return newStructure;
}

export const selectStateData = state => {
  const out = selectRequestData(state);
  return {
    ...out,
    // TODO: version?
    version: '',
    maxTermInYears: selectMaxTerm(state),
    selectedLoan: selectTermData(state),
  };
};
