import React, { useState } from 'react';
import PropTypes from 'prop-types';
import DateInput from '@aib/uikit-react/dist/Form/DateInput/DateInput';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';
import * as TS from '../../templates/Template.styled';
import ApplicantLabel from '@aib/uikit-react/dist/Form/ApplicantLabel/ApplicantLabel';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';

const MAX_AGE = 64;
const MIN_AGE = 18;

const ERROR_MESSAGE_INVALID_DATE_FORMAT = 'Please enter a valid date of birth';
const ERROR_MESSAGE_TOO_YOUNG = 'Sorry, you have to be over 18 to apply';
const ERROR_MESSAGE_TOO_OLD =
  'Sorry, the loan must be repaid by your 69th birthday to apply (min. term 5 yrs)';

function DateOfBirthStep({
  onClickContinue,
  currentStoreValue = { applicantOne: '', applicantTwo: '' },
  onChangeValue,
  isJoint,
  stepName,
}) {
  const { applicantOne = '', applicantTwo = '' } = currentStoreValue;
  const [inputValueOne, setInputValueOne] = useState(applicantOne || '');
  const [inputValueTwo, setInputValueTwo] = useState(applicantTwo || '');
  const [isValidDateOne, setIsValidDateOne] = useState(false);
  const [isValidDateTwo, setIsValidDateTwo] = useState(false);

  // DateInput does the validation
  function handleInputChangeOne({ inputValue = '', isValid }) {
    setIsValidDateOne(isValid);
    setInputValueOne(inputValue);
  }

  function handleInputChangeTwo({ inputValue = '', isValid }) {
    setIsValidDateTwo(isValid);
    setInputValueTwo(inputValue);
  }

  function handleContinueClick() {
    onChangeValue(stepName, inputValueOne, inputValueTwo);
    onClickContinue();
  }

  function isPageValid() {
    if (isJoint) {
      return isValidDateOne && isValidDateTwo;
    } else {
      return isValidDateOne;
    }
  }

  const isEnabled = isPageValid();

  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <MainQuestion
          questionText="When were you born?"
          hint="the mortgage term depends on your age"
        />
      </TS.QuestionHeader>

      <TS.QuestionContent>
        {isJoint && <ApplicantLabel applicantNumber={1} />}

        <DateInput
          onChange={handleInputChangeOne}
          value={inputValueOne}
          maxYearsMonthsDaysFromNow={[-MIN_AGE]}
          maxErrorMessage={ERROR_MESSAGE_TOO_YOUNG}
          minYearsMonthsDaysFromNow={[MAX_AGE]}
          minErrorMessage={ERROR_MESSAGE_TOO_OLD}
          invalidFormatMessage={ERROR_MESSAGE_INVALID_DATE_FORMAT}
          testId="mcx-calculator-date-input-applicant-1"
        />
      </TS.QuestionContent>

      {isJoint && (
        <TS.QuestionContent>
          <ApplicantLabel applicantNumber={2} />
          <DateInput
            onChange={handleInputChangeTwo}
            value={inputValueTwo}
            maxYearsMonthsDaysFromNow={[-MIN_AGE]}
            maxErrorMessage={ERROR_MESSAGE_TOO_YOUNG}
            minYearsMonthsDaysFromNow={[MAX_AGE]}
            minErrorMessage={ERROR_MESSAGE_TOO_OLD}
            invalidFormatMessage={ERROR_MESSAGE_INVALID_DATE_FORMAT}
            testId="mcx-calculator-date-input-applicant-2"
          />
        </TS.QuestionContent>
      )}

      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton
            onClick={handleContinueClick}
            isDisabled={!isEnabled}
          />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

DateOfBirthStep.propTypes = {
  onClickContinue: PropTypes.func.isRequired,
  currentStoreValue: PropTypes.any,
  onChangeValue: PropTypes.func.isRequired,
  isJoint: PropTypes.bool.isRequired,
  stepName: PropTypes.string.isRequired,
};

export default DateOfBirthStep;
