import React, { useState } from 'react';

import { postCalculatorData, postCalculatorState } from '../../api/restApi';
import req from '../../api/state.request.sample.json';

import * as S from './DebugToolbar.styled';

export default function DebugToolbar(args) {
  const [isDebugBarVisible, setIsDebugBarVisible] = useState(false);
  const maxWidth = isDebugBarVisible ? '' : 0;

  function onClickCalculate() {
    console.log('POST calculation');
    postCalculatorData();
  }

  function onClickSave() {
    console.log('POST save state');
    postCalculatorState(req);
  }

  function showDebugBar() {
    setIsDebugBarVisible(true);
  }

  return (
    <S.DebugToolbar onClick={showDebugBar} style={{ maxWidth: maxWidth }}>
      <h3>Debug</h3>
      <button onClick={onClickCalculate}>Calculate</button>{' '}
      <button onClick={onClickSave}>Save State</button>{' '}
    </S.DebugToolbar>
  );
}
