import { path } from 'ramda';
import {
  POST_CALCULATION_STARTED,
  POST_CALCULATION_SUCCESS,
  POST_CALCULATION_FAILED,
  POST_SAVE_STARTED,
  POST_SAVE_SUCCESS,
  POST_SAVE_FAILED,
  SET_SELECTED_TERM_INDEX,
  STORE_LOAN_PURPOSE,
  STORE_LOAN_AMOUNT,
  REMOVE_ERROR_CODE,
  RESET_RESULTS,
} from './results.types';
import {
  selectDefaultTermIndex,
  selectMaxLoanAmount,
  selectMaxTerm,
} from './results.selectors';

const initialResultsState = {
  isPostCalculationInProgress: false,
  isPostSaveInProgress: false,
  loanDetailsTermwise: [{}],
  purchaseDetails: {
    loanAmount: 0,
    purchasePrice: 0,
    loanPurpose: 'F',
  },
  selectedTermIndex: '0',
  errorCode: '',
  maxLoanAmount: 0,
  maxTerm: 0,
};

function resultsReducer(state = initialResultsState, action = {}) {
  switch (action.type) {
    case POST_CALCULATION_STARTED: {
      return { ...state, isPostCalculationInProgress: true };
    }
    case POST_CALCULATION_SUCCESS: {
      let maxLoanAmount = selectMaxLoanAmount(state);
      let maxTerm = selectMaxTerm(state);
      const loanDetailsTermwise = path(['data', 'loanDetailsTermwise'], action);
      for (var key in loanDetailsTermwise) {
        const loanAmount = loanDetailsTermwise[key].approvedLoanAmount;
        const term = loanDetailsTermwise[key].termInYears;
        maxLoanAmount = maxLoanAmount > loanAmount ? maxLoanAmount : loanAmount;
        maxTerm = maxTerm > term ? maxTerm : term;
      }
      return {
        ...state,
        loanDetailsTermwise,
        maxLoanAmount,
        maxTerm,
        isPostCalculationInProgress: false,
      };
    }
    case SET_SELECTED_TERM_INDEX: {
      // set the longest term by default:
      const termKey =
        action.data === undefined ? selectDefaultTermIndex(state) : action.data;
      return { ...state, selectedTermIndex: termKey };
    }
    case STORE_LOAN_PURPOSE: {
      return {
        ...state,
        purchaseDetails: { ...state.purchaseDetails, loanPurpose: action.data },
      };
    }
    case STORE_LOAN_AMOUNT: {
      return {
        ...state,
        purchaseDetails: { ...state.purchaseDetails, loanAmount: action.data },
      };
    }
    case POST_CALCULATION_FAILED: {
      return {
        ...state,
        errorCode: action.data,
        isPostCalculationInProgress: false,
      };
    }
    case POST_SAVE_STARTED: {
      return { ...state, isPostSaveInProgress: true };
    }
    case POST_SAVE_SUCCESS: {
      const { data } = action;
      const { mcxCalcId } = data;
      return { ...state, isPostSaveInProgress: false, mcxCalcId };
    }
    case POST_SAVE_FAILED: {
      return { ...state, errorCode: action.data, isPostSaveInProgress: false };
    }
    case REMOVE_ERROR_CODE: {
      return { ...state, errorCode: initialResultsState.errorCode };
    }
    case RESET_RESULTS: {
      return { ...initialResultsState };
    }
    default:
      return state;
  }
}

export default resultsReducer;
