import { connect } from 'react-redux';
import { path } from 'ramda';
import {
  storeLoanAmount,
  removeErrorCode,
} from '../../../store/results/results.actions';
import {
  selectMaxLoanAmount,
  selectErrorCode,
  selectMaxTerm,
  selectLoanAmount,
} from '../../../store/results/results.selectors';
import { euroValueToNumber } from '../../../util/euroFormatter';
import { postCalculatorDataThunk } from '../../../store/results/results.thunks';
import LoanAmount from './LoanAmount';

const mapStateToProps = (state, ownProps) => {
  // ? If there has been an error use the most recent loan amount that was typed
  // ? in and display the error underneath in red. If there hasn't been an error,
  // ? then use the loan amount received from the termData.
  const errorCode = selectErrorCode(state);
  let amount = ownProps.amount;
  if (errorCode) {
    const amountInEuro = selectLoanAmount(state);
    amount = euroValueToNumber(amountInEuro);
  }

  return {
    maxLoanAmount: selectMaxLoanAmount(path(['results'], state)),
    maxTerm: selectMaxTerm(path(['results'], state)),
    errorCode,
    amount,
  };
};

const mapDispatchToProps = dispatch => ({
  onBlurLoanAmount: loanAmount => {
    dispatch(removeErrorCode());
    dispatch(storeLoanAmount(loanAmount));
    dispatch(postCalculatorDataThunk());
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LoanAmount);
