import React, { useState } from 'react';
import PropTypes from 'prop-types';
import IntegerInput from '@aib/uikit-react/dist/Form/IntegerInput/IntegerInput';
import InputLabel from '@aib/uikit-react/dist/Form/InputLabel/InputLabel';
import FormControl from '@aib/uikit-react/dist/Form/FormControl/FormControl';

import * as TS from '../../templates/Template.styled';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';

import ProceedButton from '../../Form/ProceedButton/ProceedButton';

function FinancialDependantsStep({
  onClickContinue,
  currentStoreValue,
  onChangeValue,
  isJoint,
  stepName,
}) {
  const { applicantOne = '' } = currentStoreValue;
  const [inputVal, setInputVal] = useState(applicantOne || '');

  const handleInputChange = (val = '') => {
    const digits = val.replace(/\D/g, '');

    // Never show an error message
    // Only allow 2 digits
    const newValue = digits.slice(0, 2);
    setInputVal(newValue);
    onChangeValue(stepName, newValue);
  };

  const isFinished = () => Number.isInteger(parseInt(inputVal));

  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <MainQuestion
          questionText={
            isJoint
              ? 'How many children depend on both of you financially?'
              : 'How many financially dependant children do you have?'
          }
          hint={
            isJoint
              ? 'this figure includes children that you both share and/or children specific to either one of you'
              : ''
          }
        />
      </TS.QuestionHeader>

      <TS.QuestionContent>
        <FormControl>
          <InputLabel>{}</InputLabel>
          <IntegerInput
            autoFocus={false}
            fullWidth={false}
            max={99}
            min={0}
            onChange={handleInputChange}
            value={inputVal}
            testId="mcx-calculator-dependant-children-input"
          />
        </FormControl>
      </TS.QuestionContent>

      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton onClick={onClickContinue} isDisabled={!isFinished()} />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

FinancialDependantsStep.propTypes = {
  onClickContinue: PropTypes.func.isRequired,
  currentStoreValue: PropTypes.any,
  onChangeValue: PropTypes.func.isRequired,
  isJoint: PropTypes.bool.isRequired,
  stepName: PropTypes.string.isRequired,
};
export default FinancialDependantsStep;
