import { getMcxApiDomain } from '@aib/uikit-react/dist/utils/env';

const mockServerHost = 'http://localhost:3031';
// const mockServerHost = 'https://rhmcxalvd3.mid.aib.pri';
const mcxApiHost = getMcxApiDomain(window.location.host);

const isWebpackDevServer = () => typeof DEV_ENV !== 'undefined';

let _fetchImplementation = fetch;
let _apiBaseUrl = isWebpackDevServer()
  ? mockServerHost
  : `https://${mcxApiHost}`;

/* Using dependency injection, allow the host application to inject their own version of fetch.
This allows the host app full control over how the api requests are made.
For example, the AIB Mobile App team want to make the api calls themselves from the XCode/Android native code. */
export const setFetchImplementation = (fetchImplementation = fetch) => {
  _fetchImplementation = fetchImplementation;
};

/* Using dependency injection, allow the host application to specify the REST endpoint url */
export const setApiBaseUrl = apiBaseUrl => (_apiBaseUrl = apiBaseUrl);

export const postCalculatorData = body =>
  post('/credit/v1/mortgages/calculator', body);

export const postCalculatorState = body =>
  post('/credit/v1/mortgages/calculator/states', body);

function post(url = '', body = {}) {
  // Default options are marked with *
  return _fetchImplementation(_apiBaseUrl + url, {
    method: 'POST', // *GET, POST, PUT, DELETE, etc.
    mode: 'cors', // no-cors, cors, *same-origin
    cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
    credentials: 'same-origin', // include, *same-origin, omit
    headers: {
      'Content-Type': 'application/json',
      // "Content-Type": "application/x-www-form-urlencoded",
    },
    redirect: 'follow', // manual, *follow, error
    referrer: 'no-referrer', // no-referrer, *client
    body: JSON.stringify(body), // body data type must match "Content-Type" header
  }).then(response => response);
}
