import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { euroValueToNumber } from '../../../util/euroFormatter';
import { formatAsCurrency } from '@aib/uikit-react/dist/Form/CurrencyInput/currencyFormatter';
import FormControl from '@aib/uikit-react/dist/Form/FormControl/FormControl';
import apiErrorCodes from '../../../api/apiErrorCodes.json';
import * as S from './LoanAmount.styled';

const MAX_DIGITS = 8;

function LoanAmount({
  amount,
  maxLoanAmount,
  maxTerm,
  onBlurLoanAmount,
  errorCode,
  onShowMaxLoanAmount,
}) {
  const maxLoanAmountInEuro = formatAsCurrency(maxLoanAmount, '€');
  const [loanAmount, setLoanAmount] = useState(
    formatAsCurrency(amount, '€') || ''
  );

  useEffect(() => {
    handleInputChange(amount);
  }, [amount]);

  const handleInputChange = val => {
    setLoanAmount(formatAsCurrency(val, '€', MAX_DIGITS));
  };

  const handleInputBlur = () => {
    hasLoanAmountChanged() &&
      !isGreaterThanMax() &&
      onBlurLoanAmount(loanAmount);
  };

  const isMaxLoanAmount = () => loanAmount !== maxLoanAmountInEuro;
  const isGreaterThanMax = () => euroValueToNumber(loanAmount) > maxLoanAmount;
  const numberOfCharacters = () => (!loanAmount.length ? 1 : loanAmount.length);
  const hasLoanAmountChanged = () => amount !== euroValueToNumber(loanAmount);

  onShowMaxLoanAmount(isMaxLoanAmount());

  function getInfoOrErrorComponent() {
    if (!errorCode && !isGreaterThanMax()) {
      return (
        <S.LoanAmountTip>edit here if you want a lower amount</S.LoanAmountTip>
      );
    } else if (isGreaterThanMax()) {
      return (
        <S.LoanAmountError>
          Maximum amount we can lend you is {maxLoanAmountInEuro}. Please try
          again.
        </S.LoanAmountError>
      );
    } else if (errorCode) {
      return <S.LoanAmountError>{apiErrorCodes[errorCode]}</S.LoanAmountError>;
    }
  }

  return (
    <S.LoanAmountContainer>
      {isMaxLoanAmount() && (
        <S.MaxLoanAmount>
          {maxLoanAmountInEuro} <S.MaxTerm>over {maxTerm} years</S.MaxTerm>
        </S.MaxLoanAmount>
      )}
      <FormControl error={false}>
        <S.LoanAmount
          onChange={evt => handleInputChange(evt.target.value)}
          value={loanAmount}
          onBlur={() => handleInputBlur()}
          numberOfCharacters={numberOfCharacters()}
          error={!!errorCode || isGreaterThanMax()}
        />
      </FormControl>
      {getInfoOrErrorComponent()}
    </S.LoanAmountContainer>
  );
}

LoanAmount.propTypes = {
  amount: PropTypes.number.isRequired,
  maxLoanAmount: PropTypes.number.isRequired,
  maxTerm: PropTypes.number.isRequired,
  onBlurLoanAmount: PropTypes.func.isRequired,
  errorCode: PropTypes.string.isRequired,
  onShowMaxLoanAmount: PropTypes.func.isRequired,
};

export default LoanAmount;
