import {
  SAVE_ANSWER,
  GOTO_STEP_BY_NUMBER,
  GOTO_NEXT_STEP,
  GOTO_PREVIOUS_STEP,
  RESTART_CALCULATOR,
} from './questions.types';
import {
  getNextStepName,
  selectPreviousStep,
  selectNumberOfApplicants,
  getStepNameByNumber,
} from './questions.selectors';
import { stepsArray } from '../../ui/Steps/Steps.list';

const firstStep = stepsArray[0];

const initialQuestionsState = {
  currentStep: firstStep,
  isAnimationForward: true,
  steps: {},
};

function questionsReducer(state = initialQuestionsState, action = {}) {
  switch (action.type) {
    case SAVE_ANSWER: {
      const { data = {} } = action;
      let { steps = {} } = state;
      const { stepName, applicantOne, applicantTwo } = data;
      if (stepName === 'NumberOfApplicantsStep') {
        const currentNumApplicants = selectNumberOfApplicants(state);
        const updatedNumApplicants = applicantOne;
        if (updatedNumApplicants !== currentNumApplicants) {
          // https://jira.aib.pri/browse/HO-12219
          // Clear the applicant state if the number of applicants changes
          steps = {};
        }
      }
      const newSteps = { ...steps, [stepName]: { applicantOne, applicantTwo } };
      return { ...state, steps: newSteps };
    }
    case GOTO_STEP_BY_NUMBER: {
      const { stepNumber } = action;
      const stepName = getStepNameByNumber(stepNumber);
      return {
        ...state,
        currentStep: stepName,
        isAnimationForward: false,
      };
    }
    case GOTO_NEXT_STEP: {
      const nextStep = getNextStepName(state);
      return {
        ...state,
        currentStep: nextStep,
        isAnimationForward: true,
      };
    }
    case GOTO_PREVIOUS_STEP: {
      const { currentStep } = state;
      const previousStep = selectPreviousStep(currentStep);
      return { ...state, currentStep: previousStep, isAnimationForward: false };
    }
    case RESTART_CALCULATOR: {
      return { ...initialQuestionsState };
    }
    default:
      return state;
  }
}

export default questionsReducer;
