import React from 'react';
import PropTypes from 'prop-types';

import Label from '../../Form/Label/Label';
import * as icons from '../../Theme/icons';
import * as S from './Choice.styled';

function Choice({
  title,
  icon,
  idx,
  onClick,
  currentSavedValue,
  modifyButtonIcon,
  isMandatory,
}) {
  const modifyIconSrc = modifyButtonIcon ? icons[modifyButtonIcon] : undefined;

  function handleClick() {
    onClick && onClick(idx);
  }

  return (
    <S.Choice key={idx} onClick={handleClick}>
      <S.ChoiceIcon isOutlined={!!currentSavedValue}>
        {React.createElement(icon, { width: 47 })}
      </S.ChoiceIcon>

      <S.ChoiceContent hasValue={!!currentSavedValue}>
        <div className="choice-label">
          <Label text={title} />

          {!currentSavedValue && isMandatory && (
            <span style={{ color: 'red' }}>&nbsp;*</span>
          )}
        </div>

        {currentSavedValue && (
          <div className="choice-value">
            {/* TODO: Pass Euro symbol from outside */}
            &euro;
            {currentSavedValue}
          </div>
        )}
      </S.ChoiceContent>

      <div className="choice-modifier">
        {modifyButtonIcon && (
          <img width="10" height="10" src={modifyIconSrc} alt="edit" />
        )}
      </div>
    </S.Choice>
  );
}

Choice.propTypes = {
  title: PropTypes.string.isRequired,
  icon: PropTypes.func.isRequired,
  idx: PropTypes.string.isRequired,
  onClick: PropTypes.func,
  currentSavedValue: PropTypes.number,
  modifyButtonIcon: PropTypes.string,
  isMandatory: PropTypes.bool,
};

export default Choice;
