import React from 'react';
import PropTypes from 'prop-types';
import ApplicantLabel from '@aib/uikit-react/dist/Form/ApplicantLabel/ApplicantLabel';
import * as S from './SummaryListItem.styled';

function SummaryListItem({ stepName, stepValues }) {
  // Show the applicant labels if there are two sets of values
  // (which must have come from a joint application):
  const showApplicantLabels = stepValues.length === 2 && !!stepValues[1];

  const renderValue = (value, index, arr) => {
    return (
      <S.Value key={`${value + index}`}>
        {value}
        {arr && arr.length - 1 !== index && ', '}
      </S.Value>
    );
  };

  const renderValues = (values, index) => {
    return Array.isArray(values) ? (
      <S.Values key={`${values + index}`}>
        {values.map((val, i, arr) => renderValue(val, i, arr))}
      </S.Values>
    ) : (
      renderValue(values, index)
    );
  };

  return (
    <S.SummaryListItem>
      <S.Label reduceWidth={showApplicantLabels}>{stepName}</S.Label>
      <S.ValuesContainer>
        {showApplicantLabels &&
          stepValues.map((values, index) => (
            <S.ValuesWithApplicantLabel key={`${values + index}`}>
              <ApplicantLabel compact applicantNumber={index + 1} />
              {renderValues(values)}
            </S.ValuesWithApplicantLabel>
          ))}
        {!showApplicantLabels &&
          stepValues.map(values => {
            return renderValues(values);
          })}
      </S.ValuesContainer>
    </S.SummaryListItem>
  );
}

SummaryListItem.propTypes = {
  stepName: PropTypes.string.isRequired,
  stepValues: PropTypes.arrayOf(
    PropTypes.oneOfType([PropTypes.string, PropTypes.array])
  ).isRequired,
};

export default SummaryListItem;
