import PropTypes from 'prop-types';
import React from 'react';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';
import ApplicantLabel from '@aib/uikit-react/dist/Form/ApplicantLabel/ApplicantLabel';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';
import SolidRadioButtons from '@aib/uikit-react/dist/Form/SolidRadioButtons/SolidRadioButtons';
import * as TS from '../Template.styled';
import * as S from './ButtonsTemplate.styled';

function ButtonsTemplate({
  question,
  onClickContinue,
  currentStoreValue,
  onChangeValue,
  isJoint,
  stepName,
}) {
  const {
    buttons = [],
    labels,
    infoModal,
    hasApplicantTwoQuestion = true,
  } = question;

  const solidRadioOptions = buttons.map(val => ({
    value: val.text,
    label: val.text,
  }));

  const { applicantOne = '', applicantTwo = '' } = currentStoreValue;

  const handleValueChangeOne = val => {
    onChangeValue(stepName, val, applicantTwo);
  };
  const handleValueChangeTwo = val => {
    onChangeValue(stepName, applicantOne, val);
  };

  const isValid = () => {
    return isJoint && hasApplicantTwoQuestion
      ? Boolean(applicantOne && applicantTwo)
      : Boolean(applicantOne);
  };

  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <MainQuestion
          questionText={labels.mainQuestion}
          infoModal={infoModal}
        />
      </TS.QuestionHeader>

      <TS.QuestionContent>
        <S.Applicant>
          {isJoint && hasApplicantTwoQuestion && (
            <ApplicantLabel applicantNumber={1} />
          )}
          <SolidRadioButtons
            groupName="groupName"
            value={applicantOne}
            radioButtons={solidRadioOptions}
            onClick={handleValueChangeOne}
            testId="mcx-calculator-solid-radio-buttons-applicant-1"
          />
        </S.Applicant>
        {isJoint && hasApplicantTwoQuestion && (
          <S.Applicant>
            {isJoint && hasApplicantTwoQuestion && (
              <ApplicantLabel applicantNumber={2} />
            )}
            <SolidRadioButtons
              groupName="groupName"
              value={applicantTwo}
              radioButtons={solidRadioOptions}
              onClick={handleValueChangeTwo}
              testId="mcx-calculator-solid-radio-buttons-applicant-2"
            />
          </S.Applicant>
        )}
      </TS.QuestionContent>

      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton
            onClick={onClickContinue}
            buttonLabel={labels.nextButton}
            isDisabled={!isValid()}
          />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

ButtonsTemplate.propTypes = {
  question: PropTypes.object.isRequired,
  onClickContinue: PropTypes.func.isRequired,
  currentStoreValue: PropTypes.any,
  onChangeValue: PropTypes.func.isRequired,
  isJoint: PropTypes.bool.isRequired,
  stepName: PropTypes.string.isRequired,
};

export default ButtonsTemplate;
