import { connect } from 'react-redux';
import {
  saveAnswer,
  gotoNextStep,
} from '../../../store/questions/questions.actions';
import {
  selectIsAnimationForward,
  selectCurrentStep,
  selectCurrentQuestionData,
  selectCurrentAnswer,
  isJoint,
} from '../../../store/questions/questions.selectors';
import Step from './Step';

const mapDispatchToProps = {
  onChangeValue: saveAnswer,
  onClickContinue: gotoNextStep,
};

const mapStateToProps = state => {
  const currentQuestionData = selectCurrentQuestionData(state);
  const stepName = selectCurrentStep(state);
  return {
    isAnimationForward: selectIsAnimationForward(state),
    currentQuestionData,
    currentStoreValue: selectCurrentAnswer(state),
    isJoint: isJoint(state),
    stepName,
  };
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Step);
