import {
  POST_CALCULATION_STARTED,
  POST_CALCULATION_SUCCESS,
  POST_CALCULATION_FAILED,
  POST_SAVE_STARTED,
  POST_SAVE_SUCCESS,
  POST_SAVE_FAILED,
  SET_SELECTED_TERM_INDEX,
  STORE_LOAN_PURPOSE,
  STORE_LOAN_AMOUNT,
  REMOVE_ERROR_CODE,
  RESET_RESULTS,
} from './results.types';

export const postCalculationStarted = data => ({
  type: POST_CALCULATION_STARTED,
  data,
});

export const postCalculationSuccess = data => ({
  type: POST_CALCULATION_SUCCESS,
  data,
});

export const postCalculationFailed = data => ({
  type: POST_CALCULATION_FAILED,
  data,
});

export const postSaveStarted = data => ({
  type: POST_SAVE_STARTED,
  data,
});

export const postSaveSuccess = data => ({
  type: POST_SAVE_SUCCESS,
  data,
});

export const postSaveFailed = data => ({
  type: POST_SAVE_FAILED,
  data,
});

export const setSelectedTermIndex = data => ({
  type: SET_SELECTED_TERM_INDEX,
  data,
});

export const storeLoanPurpose = data => ({
  type: STORE_LOAN_PURPOSE,
  data,
});

export const storeLoanAmount = data => ({
  type: STORE_LOAN_AMOUNT,
  data,
});

export const removeErrorCode = () => ({ type: REMOVE_ERROR_CODE });

export const resetResults = () => ({ type: RESET_RESULTS });
