import React from 'react';
import PropTypes from 'prop-types';
import Choice from '../../Form/Choice/Choice';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';
import Label from '../../Form/Label/Label';
import * as TS from '../../templates/Template.styled';
import question from './IntroStep.question';

function IntroStep({ onClickContinue }) {
  const { smallPrint, labels, bullets = new Map() } = question;

  function renderBullets() {
    const res = [];
    bullets.forEach((icon, title) => {
      res.push(<Choice title={title} icon={icon} idx={title} key={title} />);
    });
    return res;
  }

  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <Label text={labels.mainQuestion} />
      </TS.QuestionHeader>
      <TS.QuestionContent>{renderBullets()}</TS.QuestionContent>
      <TS.QuestionFooter>
        <TS.QuestionFooterLabel>
          <Label text={labels.label4} />
        </TS.QuestionFooterLabel>

        {smallPrint && <TS.Smallprint>{smallPrint}</TS.Smallprint>}
        <TS.QuestionFooterLabel>
          <ProceedButton
            onClick={onClickContinue}
            buttonLabel={labels.nextButton || ''}
          />
        </TS.QuestionFooterLabel>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

IntroStep.propTypes = {
  onClickContinue: PropTypes.func.isRequired,
};

export default IntroStep;
