import React, { useRef } from 'react';
import PropTypes from 'prop-types';
import * as S from './PageBody.styled';

function PageBody({ children }) {
  const pageBodyElem = useRef(null);

  // scroll to the top when you go to the next page:
  if (pageBodyElem.current) {
    pageBodyElem.current.scrollTop = 0;
  }

  return <S.PageBody ref={pageBodyElem}>{children}</S.PageBody>;
}

PageBody.propTypes = {
  children: PropTypes.any.isRequired,
};

export default PageBody;
