import styled, { keyframes } from 'styled-components';
import Input from '@aib/uikit-react/dist/Form/Input/Input';
import {
  fontFamily,
  color,
  fontSize,
  lineHeight,
} from '@aib/design-tokens/dist/design-tokens';

// ? if this keyframes is ever required elsewhere, then it should be moved to a common place
const fadeIn = keyframes`
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
`;

export const LoanAmountContainer = styled.div`
  text-align: center;
  margin: 0 0 1rem 0;
  padding-bottom: 2rem;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
`;

// Override the UI Kit's Input component styles
export const LoanAmount = styled(Input)`
  /* Font gets smaller when the euro formatted number is longer than 8 characters: ie. '€XXX,XXX' */
  font-size: ${props =>
    props.numberOfCharacters > 8 ? fontSize.xxxLarge : fontSize.xxxxLarge};
  color: ${props => (props.error ? color.red100 : color.darkGreen100)};
  border-bottom: 4px solid
    ${props => (props.error ? color.red100 : color.darkGreen100)};
  display: inline-block;
  width: ${props => props.numberOfCharacters + 'ch'};
  padding-left: 0px;
  padding-right: 0px;
  text-align: center;

  &:focus-within {
    border-bottom: 4px solid
      ${props => (props.error ? color.red100 : color.darkGreen100)};
  }
`;

const LoanAmountMessage = styled.div`
  font-size: ${fontSize.small};
  margin: 0.5rem 1rem 0;
`;

export const LoanAmountTip = styled(LoanAmountMessage)`
  font-style: italic;
  color: ${color.grey05};
`;

export const LoanAmountError = styled(LoanAmountMessage)`
  font-family: ${fontFamily.bold};
  color: ${color.red100};
`;

export const MaxLoanAmount = styled.div`
  font-size: ${fontSize.xxLarge};
  line-height: ${lineHeight.xxLarge};
  color: ${color.grey06};
  text-align: center;
  margin: 0 0 1rem;
  animation: 0.4s ${fadeIn} ease-in;
`;

export const MaxTerm = styled.div`
  font-size: ${fontSize.small};
  line-height: ${lineHeight.small};
  margin-top: 0.25rem;
`;
