import React, { useState } from 'react';
import PropTypes from 'prop-types';
import Modal from '@aib/uikit-react/dist/Animations/Modal/Modal';
import InfoPopupView from '@aib/uikit-react/dist/Content/InfoPopupView/InfoPopupView';
import * as S from './MainQuestion.styled';
import Link from '@aib/uikit-react/dist/Navigation/Link/Link';

function MainQuestion(props) {
  const [isModalVisible, setIsModalVisible] = useState(false);

  function handleLinkClick(e) {
    e.preventDefault();
    setIsModalVisible(true);
  }

  const textArray = props.questionText.split('{a}');
  const opener = textArray[0];
  const closer = textArray[1];

  return (
    <S.MainContainer centered={props.centered}>
      <S.Question hasSmallerText={props.hasSmallerText}>
        <span>{opener}</span>
        {props.infoModal && (
          <Link onClick={handleLinkClick}>{props.infoModal.title}</Link>
        )}

        <Modal open={isModalVisible}>
          {props.infoModal ? (
            <InfoPopupView
              title={props.infoModal.title}
              body={props.infoModal.body}
              buttonText={props.infoModal.buttonText}
              onClick={() => setIsModalVisible(false)}
            />
          ) : (
            <React.Fragment />
          )}
        </Modal>
        {closer && <span>{closer}</span>}
      </S.Question>
      {props.hint && <S.Hint>({props.hint})</S.Hint>}
    </S.MainContainer>
  );
}

MainQuestion.propTypes = {
  questionText: PropTypes.string.isRequired,
  hint: PropTypes.string,
  infoModal: PropTypes.object,
  centered: PropTypes.bool,
  hasSmallerText: PropTypes.bool,
};

export default MainQuestion;
