import React from 'react';
import PropTypes from 'prop-types';
import Link from '@aib/uikit-react/dist/Navigation/Link/Link';

function Label({ text, onClickLink }) {
  // no props or label defined, return empty html
  if (!text) {
    return '';
  }

  // auto-return strings
  if (typeof text === 'string') {
    return text;
  }

  // map shuffled strings and links
  if (Array.isArray(text)) {
    return text.map((item, i) => {
      return typeof item === 'string' ? (
        <span key={i}>{item}</span>
      ) : (
        // ? I don't think the below functionality is used anywhere...
        <Link key={i} href={item.linkUrl} onClick={onClickLink} size="medium">
          {item.linkText}
        </Link>
      );
    });
  }
}

Label.propTypes = {
  text: PropTypes.any,
  onClickLink: PropTypes.func,
};

export default Label;
