import { GOTO_NEXT_STEP } from '../store/questions/questions.types';
import { getNextStepName } from '../store/questions/questions.selectors';

export const analyticsMiddleware = store => next => action => {
  const state = store.getState();
  const dataLayer = window.dataLayer || [];
  switch (action.type) {
    case GOTO_NEXT_STEP: {
      const nextStepName = getNextStepName(state);
      dataLayer.push({ event: 'Pageview', pageName: nextStepName });
      break;
    }
    default: {
      break;
    }
  }
  next(action);
};
