import { connect } from 'react-redux';
import {
  selectTermData,
  selectSelectedTermIndex,
  selectRangeData,
  selectErrorCode,
  selectIsPostCalculationInProgress,
  selectIsPostSaveInProgress,
  selectLoanPurpose,
} from '../../../store/results/results.selectors';
import {
  setSelectedTermIndex,
  storeLoanPurpose,
  removeErrorCode,
} from '../../../store/results/results.actions';
import {
  postCalculatorDataThunk,
  postCalculatorStateThunk,
  GENERIC_ERROR_CODE,
} from '../../../store/results/results.thunks';
import outsidePolicyErrorCodes from '../../../api/outsidePolicyErrorCodes';
import ResultsStep from './ResultsStep';

const mapStateToProps = state => {
  const errorCode = selectErrorCode(state);
  const isOutsidePolicy = outsidePolicyErrorCodes.includes(errorCode);
  const isCriticalError = GENERIC_ERROR_CODE === errorCode;

  return {
    termData: selectTermData(state),
    selectedTermIndex: selectSelectedTermIndex(state),
    rangeData: selectRangeData(state),
    isOutsidePolicy,
    isCriticalError,
    isPostCalculationInProgress: selectIsPostCalculationInProgress(state),
    isPostSaveInProgress: selectIsPostSaveInProgress(state),
    selectedLoanPurpose: selectLoanPurpose(state),
  };
};

const mapDispatchToProps = dispatch => ({
  onChangeRange: value => {
    dispatch(setSelectedTermIndex(value));
  },
  onChangeTab: loanPurposeId => {
    dispatch(storeLoanPurpose(loanPurposeId));
    dispatch(postCalculatorDataThunk());
  },
  onClickContinue: () => {
    dispatch(postCalculatorStateThunk());
  },
  onClickCriticalErrorAlert: () => {
    dispatch(removeErrorCode());
  },
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ResultsStep);
