import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import ProgressBarConnected from './ProgressBarConnected';

const ProgressBarStyled = styled.div`
  margin-top: ${props => (props.isNavBarVisible ? '44px' : '0px')};
  box-sizing: border-box;
  padding: 0px 37px;
`;

const ProgressBarContainer = function({ isNavBarVisible = true }) {
  return (
    <ProgressBarStyled isNavBarVisible={isNavBarVisible}>
      <ProgressBarConnected />
    </ProgressBarStyled>
  );
};

ProgressBarContainer.propTypes = {
  isNavBarVisible: PropTypes.bool,
};

export default ProgressBarContainer;
