import React from 'react';
import PropTypes from 'prop-types';
import * as TS from '../../templates/Template.styled';
import * as S from './SummaryStep.styled';
import SummaryListItem from '../../BuildingBlocks/SummaryListItem/SummaryListItem';
import MainQuestion from '../../Form/MainQuestion/MainQuestion';
import ProceedButton from '../../Form/ProceedButton/ProceedButton';
import mapStepToReadableName from './mapStepToReadableName.json';

function SummaryStep({
  aboutYouData,
  incomeData,
  outgoingsData,
  onClickProceed,
}) {
  return (
    <TS.QuestionContainer>
      <TS.QuestionHeader>
        <MainQuestion questionText="Here is the information you told us, please review before the calculation" />
      </TS.QuestionHeader>
      <TS.QuestionContent>
        <S.SummaryBlock>
          <S.SummarySubHeading>About you</S.SummarySubHeading>
          {Object.keys(aboutYouData).map((step, index) => (
            <SummaryListItem
              key={index}
              stepValues={[
                aboutYouData[step].applicantOne,
                aboutYouData[step].applicantTwo,
              ]}
              stepName={mapStepToReadableName[step]}
            />
          ))}
        </S.SummaryBlock>
        <S.SummaryBlock>
          <S.SummarySubHeading>Income</S.SummarySubHeading>
          {Object.keys(incomeData).map((step, index) => (
            <SummaryListItem
              key={index}
              stepValues={[
                incomeData[step].applicantOne,
                incomeData[step].applicantTwo,
              ]}
              stepName={mapStepToReadableName[step]}
            />
          ))}
        </S.SummaryBlock>
        <S.SummaryBlock>
          <S.SummarySubHeading>Outgoings</S.SummarySubHeading>
          {Object.keys(outgoingsData).map((step, index) => (
            <SummaryListItem
              key={index}
              stepValues={[
                outgoingsData[step].applicantOne,
                outgoingsData[step].applicantTwo,
              ]}
              stepName={mapStepToReadableName[step]}
            />
          ))}
        </S.SummaryBlock>
      </TS.QuestionContent>
      <TS.QuestionFooter>
        <TS.QuestionFooterButton>
          <ProceedButton
            onClick={onClickProceed}
            buttonLabel="Confirm and calculate"
          />
        </TS.QuestionFooterButton>
      </TS.QuestionFooter>
    </TS.QuestionContainer>
  );
}

SummaryStep.propTypes = {
  aboutYouData: PropTypes.object.isRequired,
  incomeData: PropTypes.object.isRequired,
  outgoingsData: PropTypes.object.isRequired,
  onClickProceed: PropTypes.func.isRequired,
};

export default SummaryStep;
