import { getMcxApiDomain } from '@aib/uikit-react/dist/utils/env';
import {
  selectRequestData,
  selectStateData,
} from '../questions/questions.selectors';
import {
  postCalculationStarted,
  postCalculationSuccess,
  postCalculationFailed,
  postSaveStarted,
  postSaveSuccess,
  postSaveFailed,
  setSelectedTermIndex,
} from './results.actions';
import { postCalculatorData, postCalculatorState } from '../../api/restApi';

const DATA_ERROR = 'DataError';
export const GENERIC_ERROR_CODE = '1';
const mcxApiHost = getMcxApiDomain(window.location.host);

export const postCalculatorDataThunk = () => {
  return (dispatch, getState) => {
    const requestData = selectRequestData(getState());
    dispatch(postCalculationStarted());
    postCalculatorData(requestData)
      .then(parseResponse)
      .then(raiseErrors)
      .then(responseBody => {
        dispatch(postCalculationSuccess(responseBody));
        dispatch(setSelectedTermIndex());
      })
      .catch(error => {
        if (!error.errorCode && error.name !== DATA_ERROR) {
          dispatch(postCalculationFailed(GENERIC_ERROR_CODE));
          return;
        }
        dispatch(postCalculationFailed(error.errorCode));
      });
  };
};

export const postCalculatorStateThunk = () => {
  return (dispatch, getState) => {
    const requestData = selectStateData(getState());
    dispatch(postSaveStarted());
    postCalculatorState(requestData)
      .then(parseResponse)
      .then(raiseErrors)
      .then(responseBody => {
        dispatch(postSaveSuccess(responseBody));
        dispatch(setSelectedTermIndex());

        // TODO:
        window.open(
          `https://${mcxApiHost}/apply?mcxCalcId=${responseBody.mcxCalcId}`,
          '_self'
        );
      })
      .catch(error => {
        console.error({ error });
        dispatch(postSaveFailed(GENERIC_ERROR_CODE));
      });
  };
};

function parseResponse(response) {
  return {
    responseBody: response.json(),
    responseStatus: response.status,
  };
}

function raiseErrors(parsedResponse) {
  const responseBody = parsedResponse.responseBody;
  switch (parsedResponse.responseStatus) {
    case 400:
      throw new DataError(responseBody.code);
    case 500:
      throw new DataError(GENERIC_ERROR_CODE);
    default:
      break;
  }
  return responseBody;
}

function DataError(code) {
  this.errorCode = code;
  this.name = DATA_ERROR;
}
