import { path } from 'ramda';

export const selectTermData = (state = {}) => {
  const selectedTermIndex = selectSelectedTermIndex(state);
  return path(['results', 'loanDetailsTermwise', selectedTermIndex], state);
};

export const selectSelectedTermIndex = (state = {}) =>
  path(['results', 'selectedTermIndex'], state);

export const selectDefaultTermIndex = (state = {}) => {
  const loanDetails = path(['loanDetailsTermwise'], state);
  // The default term when entering the page should be the longest term:
  return loanDetails.length - 1;
};

export const selectRangeData = (state = {}) => {
  let rangeData = [];
  const loanDetails = path(['results', 'loanDetailsTermwise'], state);
  for (var key in loanDetails) {
    rangeData.push(loanDetails[key].termInYears);
  }
  return rangeData;
};

export const selectMaxLoanAmount = (state = {}) =>
  path(['maxLoanAmount'], state);

export const selectLoanAmount = (state = {}) =>
  path(['results', 'purchaseDetails', 'loanAmount'], state);

export const selectLoanPurpose = (state = {}) =>
  path(['results', 'purchaseDetails', 'loanPurpose'], state);

export const selectMaxTerm = (state = {}) => {
  // TODO: why does 'state' refer to the top level of the store
  // when in a connected component but refers to a limb when in a reducer?
  const results = state.results || state;
  return path(['maxTerm'], results);
};
export const selectErrorCode = (state = {}) =>
  path(['results', 'errorCode'], state);

export const selectIsPostCalculationInProgress = (state = {}) =>
  path(['results', 'isPostCalculationInProgress'], state);

export const selectIsPostSaveInProgress = (state = {}) =>
  path(['results', 'isPostSaveInProgress'], state);
