import React from 'react';
import IconPhone from '@aib/uikit-react/dist/Icons/IconPhone';
import IconCallback from '@aib/uikit-react/dist/Icons/IconCallback';
import * as S from './ContactMethods.styled';

const AIB_PHONE_NUMBER = '1890 24 24 25';

function ContactMethods() {
  const contactMethods = [
    {
      label: 'Schedule a callback',
      icon: <IconCallback color="white" fillColor="white" />,
      href: 'https://aib.ie/personal-forms/mortgageform',
      openInNewTab: true,
    },
    {
      label: (
        <React.Fragment>
          <div>Call us</div>
          <div>{AIB_PHONE_NUMBER}</div>
        </React.Fragment>
      ),
      icon: <IconPhone color="white" fillColor="white" />,
      href: `tel:${AIB_PHONE_NUMBER}`,
      openInNewTab: false,
    },
  ];

  return (
    <S.ContactMethods>
      {contactMethods.map((contactMethod, i) => (
        <S.ContactMethod
          key={i}
          textUnderline={false}
          href={contactMethod.href}
          openInNewTab={contactMethod.openInNewTab}
        >
          <S.IconContainer>{contactMethod.icon}</S.IconContainer>
          <S.ContactLabel>{contactMethod.label}</S.ContactLabel>
        </S.ContactMethod>
      ))}
    </S.ContactMethods>
  );
}

export default ContactMethods;
